import React from 'react'
import {Link} from 'react-router-dom'
export default class Footer extends React.Component{
    render(){
        return (
            <div>
                 <Link to='/movie'></Link>
                 <Link to='/cinema'></Link>
               <Link to='/mine'></Link>                
            </div>
        )
    }
}


